import classNames from 'classnames';
import React from 'react';

export const detailNavStyles = 'bg-primary';

export const DetailNav: React.FC<React.HTMLAttributes<HTMLDivElement>> = ({
  className,
  children,
  ...props
}) => (
  <nav className={classNames(detailNavStyles, className)} {...props}>
    {children}
  </nav>
);
